@import url(https://cdn.jsdelivr.net/npm/bootstrap@4.6.0/dist/css/bootstrap.min.css);

.header-top {
  height: 100px;
}

header {
  background-color: #a2998d;
  opacity: 100%;
  min-height: 50px;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  border-bottom: 1px solid#a2998d;
  padding: 0 180px;
  z-index: 10;
  position: relative;
}

header .logo {
  flex: 1;
}

header a {
  text-transform: uppercase;
  color: #e5e5e5;
}

header ul li {
  display: inline-block;
  opacity: 1;
  padding: 0 20px;
}

header ul li:hover {
  opacity: 0.7;
}

/* li>a:hover {
  opacity: 0.7;
  color: #fff;
} */

.menu {
  display: none;
}

.cart-icon {
  position: relative;
  margin-right: 20px;
}

.cart-icon span {
  background: crimson;
  border-radius: 20px;
  color: white;
  position: absolute;
  top: -10px;
  right: -10px;
  padding: 5px 7px;
  font-size: 8px;
}

/* sidebar */
.text-primary {
  color: #5a8dee !important;
}

.btn-primary {
  border-color: #5a8dee !important;
  background-color: #ff3d00 !important;
  color: #fff;
}

.shadow-md {
  box-shadow: 0px 5px 10px rgba(90, 116, 148, 0.3);
}

.sidebar {
  width: 280px;
  min-height: 100vh;
  box-shadow: 0px 4px 8px rgb(0 0 0 / 16%);
  background-color: #fff;
  position: fixed;
  top: 0;
  left: -100%;
  z-index: 1;
  transition: 0.5s;
}

.sidebar.active {
  left: 0;
}

.sd-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
}

.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
}

/* effect khi bam */
.sidebar-overlay.active {
  opacity: 1;
  visibility: visible;
}

.sd-body ul li a {
  color: #080808 !important;
  /* border-bottom: 1px solid #524eb7; */
  padding-bottom: 10px;
}

.sd-body {
  padding: 15px;
  max-height: calc(100vh - 67px);
  overflow-x: hidden;
}

.sd-body ul {
  display: inline-block;
  width: 100%;
  margin-bottom: 0;
  padding: 0;
}

.sd-body ul li {
  list-style: none;
  margin-bottom: 8px;
}

.sd-body ul li .sd-link {
  display: inline-block;
  width: 100%;
  padding: 10px 16px;
  color: #080808;
  background-color: #e5e8ec;
  border-radius: 4px;
  cursor: pointer;
  text-decoration: none;
}

header>ul {
  position: absolute;
  left: 37% !important;
  margin-bottom: 0px;
}